.message_time {
  padding: 15px 0;
  text-align: center;
}
.message_time > span {
  padding: 4px 8px;
  background-color: #DFDFDF;
  border-radius: 4px;
  color: #FFF;
}
.message_content_noread,
.message_content_isread {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
}
.message_content_noread {
  background-color: #FF0000;
}
.message_content_isread {
  background-color: #34A5EA;
}
.message_content_view {
  color: #34A5EA;
}
